import algoliasearch from 'algoliasearch';

export interface AlgoliaConfig {
  applicationId: string;
  apiKey: string;
  indexName: string;
}

export interface AlgoliaSearchResult {
  objectID: string;
  name?: string;
  sku?: string;
  price?: number;
  image?: string;
  url?: string;
  [key: string]: any;
}

export interface AlgoliaSearchResponse {
  results: AlgoliaSearchResult[];
  nbHits: number;
  hitsPerPage: number;
  page: number;
}

let client: ReturnType<typeof algoliasearch> | null = null;
let index: any = null;
let config: AlgoliaConfig | null = null;

/**
 * Initialize Algolia client with configuration
 */
export const initializeAlgolia = (algoliaConfig: AlgoliaConfig) => {
  config = algoliaConfig;
  client = algoliasearch(algoliaConfig.applicationId, algoliaConfig.apiKey);
  index = client.initIndex(algoliaConfig.indexName);
};

/**
 * Search for products in Algolia index
 */
export const searchProducts = async (
  query: string,
  options?: { hitsPerPage?: number; page?: number }
): Promise<AlgoliaSearchResponse> => {
  if (!client || !index) {
    throw new Error(
      'Algolia client not initialized. Call initializeAlgolia first.'
    );
  }

  if (!query.trim()) {
    return { results: [], nbHits: 0, hitsPerPage: 10, page: 0 };
  }

  try {
    const response = await index.search(query, {
      hitsPerPage: options?.hitsPerPage || 10,
      page: options?.page || 0,
    });

    return {
      results: response.hits || [],
      nbHits: response.nbHits || 0,
      hitsPerPage: response.hitsPerPage || 10,
      page: response.page || 0,
    };
  } catch (error) {
    console.error('Algolia search error:', error);
    throw error;
  }
};

/**
 * Get the current Algolia configuration
 */
export const getAlgoliaConfig = (): AlgoliaConfig | null => config;
